.ticker {
  width: 100vw;
  overflow: hidden;
  
  .rfm-child {
    display: flex;
    margin: 30px;
  }

  .testimonial-card {
    flex: 0 0 auto;
    transition: transform 0.3s ease, box-shadow 0.3s ease;

    &:hover {
      transform: scale(1.05);
      box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
    }
  }
}
