.footer {
    @include flex();
    background: linear-gradient(to right, rgba($purple-secondary, 0.8) 0%, rgba($violet, 0.8) 100%);
    width: 100%;

    &__content {
        @include flex($gap: 40px);
        max-width: 1440px;
        width: 100%;
        padding: 60px 50px 40px;
    }

    &__top {
        @include flex($gap: 35px);

        &--logo {
            @include flex($direction: row, $gap: 10px);

            img {
                height: 82px;
                width: 82px;
                transition: transform 0.2s ease-out;
            }

            &-text {
                @include flex($gap: 0px, $align: start);

                h2 {
                    @include title-font($size: 30px, $weight: 400, $color: #fff);
                    line-height: 30px;
                }

                h3 {
                    @include title-font($size: 30px, $color: #fff);
                    line-height: 30px;
                }
            }
        }

        p {
            @include body-font($size: 20px, $color: #fff);
            line-height: 24px;
            max-width: 650px;
            text-align: center;
        }
    }

    hr {
        width: 100%;
        border: 1px solid #fff;
    }

    &__bottom {
        @include flex($direction: row, $justify: space-between, $align: start);
        width: 100%;
        max-width: 950px;

        h2 {
            @include body-font($size: 20px, $weight: 700, $color: #fff);
            letter-spacing: 3px;
        }
        
        p {
            @include body-font($size: 18px, $weight: 500, $color: #fff);
            line-height: 22px;
            max-width: 250px;
        }

        &--contact {
            @include flex($align: start, $gap: 15px);
            
            &-items {
                @include flex($align: start, $gap: 15px);
            }

            &-item {
                @include flex($direction: row, $gap: 15px);
            }
        }

        &--links {
            @include flex($align: start, $gap: 15px);

            &-line {
                background-color: white;
                width: 4px;
                height: 30px;
            }
            
            &-items {
                @include flex($align: start, $gap: 10px);
            }

            &-item {
                @include flex($direction: row, $gap: 10px);
                cursor: pointer;
                transition: transform 0.2s ease, color 0.3s ease;

                &:hover {
                    transform: translateX(5px); 
                }
            }
        }

        &--social {
            @include flex($align: start, $gap: 15px);

            &-items {
                @include flex($align: start, $gap: 15px);
            }
        }
    }

    &__copyright {
        @include body-font($size: 16px, $color: #fff);
        line-height: 20px;
        text-align: center;
        padding-top: 8px;
    }
}